import { dateTimeFormatter } from '../utils/dateTimeFormatters';

interface HeadCell {
  id: string;
  disablePadding: boolean;
  orderField?: keyof Email;
  getData: (row: Email) => { data: JSX.Element | string; title?: string };
  label: string;
  numeric: boolean;
}

const headCells: readonly HeadCell[] = [
  {
    id: 'state',
    orderField: 'state',
    getData: (row) => ({ data: `${row.state}` }),
    numeric: false,
    disablePadding: true,
    label: 'Email State',
  },
  {
    id: 'createdAt',
    orderField: 'createdAt',
    getData: (row) => {
      const firstRow = `${
        row.createdAt ? dateTimeFormatter.format(new Date(row.createdAt)) : ''
      }`;
      return {
        data: <span>{`${firstRow}`}</span>,
        title: firstRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Created At',
  },
  {
    id: 'updatedAt',
    orderField: 'updatedAt',
    getData: (row) => {
      const firstRow = `${
        row.updatedAt ? dateTimeFormatter.format(new Date(row.updatedAt)) : ''
      }`;
      return {
        data: <span>{`${firstRow}`}</span>,
        title: firstRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Updated At',
  },
  {
    id: 'from',
    getData: (row) => {
      const firstRow = row.from;
      return {
        data: <span>{`${firstRow}`}</span>,
        title: firstRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'From',
  },
  {
    id: 'subject',
    getData: (row) => {
      const firstRow = row.subject;
      return {
        data: <span>{`${firstRow}`}</span>,
        title: firstRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Subject',
  },
  {
    id: 'to',
    getData: (row) => {
      const firstTo = row.to && row.to[0];
      if (firstTo?.toType === 'Person') {
        const to = firstTo.to as Person;
        const firstRow = `${to.fullName}`;
        const secondRow = `${getType(to)}`;
        const path = `./${getPath(to)}/view/${to.id}`;
        return {
          data: (
            <span>
              <a href={path} target="_blank" onClick={stopPropagation}>
                {`${firstRow}`}
              </a>
              <br />
              {`${secondRow}`}
            </span>
          ),
          title: firstRow + '\n' + secondRow,
        };
      }
      if (firstTo?.toType === 'User') {
        const to = firstTo.to as User;
        const firstRow = `${to.fullName}`;
        const secondRow = `${to.userRole}`;
        const path = `./users/view/${to.id}`;
        return {
          data: (
            <span>
              <a href={path} target="_blank" onClick={stopPropagation}>
                {`${firstRow}`}
              </a>
              <br />
              {`${secondRow}`}
            </span>
          ),
          title: firstRow + '\n' + secondRow,
        };
      }
      return { data: '' };
    },
    numeric: false,
    disablePadding: true,
    label: 'First To',
  },
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof Email,
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order | undefined;
  orderBy: string | undefined;
  rowCount: number;
}

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundColor: theme.palette.background.default,
}));

const StyledTableCell = styled(TableCell)(() => ({
  maxWidth: '100px',
  paddingTop: '6px',
  paddingBottom: '6px',
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
  overflow: 'hidden',
}));
function EnhancedTableHead(props: EnhancedTableProps) {
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
  } = props;
  const createSortHandler =
    (property: keyof Email) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <StyledTableHead>
      <TableRow>
        <StyledTableCell padding="checkbox">
          <Checkbox
            name={'enhanced-table-checkbox-select-all'}
            color="primary"
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{
              'aria-label': 'enhanced-table-checkbox-select-all',
            }}
          />
        </StyledTableCell>
        {headCells.map((headCell) => (
          <StyledTableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'center'}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={headCell.orderField && orderBy === headCell.orderField}
              hideSortIcon={!headCell.orderField}
              direction={orderBy === headCell.orderField ? order : 'asc'}
              onClick={
                headCell.orderField
                  ? createSortHandler(headCell.orderField)
                  : () => {
                      return;
                    }
              }
            >
              {headCell.label}
              {headCell.orderField && orderBy === headCell.orderField ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </StyledTableCell>
        ))}
      </TableRow>
    </StyledTableHead>
  );
}

